.page-header {
  min-height: 878px;

  background-color: #1B2D37;
  background-image: url("../img/desktop-promo.png");
  background-repeat: no-repeat;
  background-position: 50% 0%;
}

.page-header__wrapper {
  margin: 0 auto;
  max-width: 1600px;

  display: flex;
  flex-direction: column;
}

.page-header__nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  margin-top: 32px;
  margin-right: 6%;
  margin-left: 6%;
}

@media (max-width: 1024px) {
  .page-header__nav {
    margin-top: 20px;
    margin-right: 4%;
    margin-left: 3%;
  }
}

@media (max-width: 768px) {
  .page-header {
    min-height: 500px;
  }

  .page-header__nav {
    margin-top: 17px;
    margin-right: 5%;
    margin-left: 6%;
  }
}

