@charset "UTF-8";
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: local("Roboto"), url("../fonts/Roboto-400regular.woff2") format("woff2"), url("../fonts/Roboto-400regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: local("Roboto"), url("../fonts/Roboto-500medium.woff2") format("woff2"), url("../fonts/Roboto-500medium.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  src: local("Roboto"), url("../fonts/Roboto-700bold.woff2") format("woff2"), url("../fonts/Roboto-700bold.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: local("Roboto Condensed"), url("../fonts/RobotoCondensed-400regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-400regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: local("Roboto Condensed"), url("../fonts/RobotoCondensed-700bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-700bold.woff") format("woff"); }

@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  src: local("PT Mono"), url("../fonts/PTMono-400regular.woff2") format("woff2"), url("../fonts/PTMono-400regular.woff") format("woff"); }

body {
  font-family: "PT Mono", "PT Serif", monospace;
  max-width: 1920px;
  margin: 0;
  padding: 0;
  margin: 0 auto; }

.page-header {
  min-height: 878px;
  background-color: #1B2D37;
  background-image: url("../img/desktop-promo.png");
  background-repeat: no-repeat;
  background-position: 50% 0%; }

.page-header__wrapper {
  margin: 0 auto;
  max-width: 1600px;
  display: flex;
  flex-direction: column; }

.page-header__nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 32px;
  margin-right: 6%;
  margin-left: 6%; }

@media (max-width: 1024px) {
  .page-header__nav {
    margin-top: 20px;
    margin-right: 4%;
    margin-left: 3%; } }

@media (max-width: 768px) {
  .page-header {
    min-height: 500px; }
  .page-header__nav {
    margin-top: 17px;
    margin-right: 5%;
    margin-left: 6%; } }

.logo {
  display: flex;
  flex-direction: row; }

.logo__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 16px;
  width: 202px; }

.logo__title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  color: #ffffff;
  font-size: 25px;
  line-height: 29px; }

.logo__subtitle {
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  color: #788891;
  font-size: 12px;
  line-height: 14px; }

@media (max-width: 768px) {
  .logo__image {
    width: 38px;
    height: 38px; }
  .logo__wrapper {
    display: none; } }

.info-company {
  display: flex;
  flex-direction: row;
  align-items: center; }

.info-company__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style: none;
  font-family: "PT Mono", "PT Serif", monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 1.125; }

.info-company__item {
  margin-left: 122px; }

.info-company__link {
  position: relative;
  color: #ffffff;
  text-decoration: none; }

.info-company__icon {
  position: absolute;
  left: -38px; }

.info-company__button {
  min-height: 45px;
  padding: 14px 26px;
  margin-left: 50px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background: none;
  outline: none;
  border: 1px solid #ffffff;
  box-sizing: border-box;
  border-radius: 5px; }

.info-company__button:hover {
  background-color: rgba(255, 255, 255, 0.1); }

.info-company__button:active {
  background-color: rgba(255, 255, 255, 0.2); }

@media (max-width: 1024px) {
  .info-company__item {
    padding: 0;
    margin-left: 35px; }
  .info-company__icon {
    position: relative;
    left: 0;
    width: 23px;
    height: 23px; }
  .info-company__icon--mail {
    width: 25px; }
  .info-company__text {
    display: none; }
  .info-company__button {
    display: none; } }

@media (max-width: 768px) {
  .info-company {
    align-items: flex-start;
    margin-top: 5px; }
  .info-company__item {
    margin-left: 25px; } }

.promo {
  position: relative;
  display: flex;
  flex-direction: row;
  margin-top: 170px;
  margin-left: 5.7%;
  margin-right: 1%; }

.promo__content {
  margin-right: 71px; }

.promo__title {
  margin-bottom: 30px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 80px;
  line-height: 80px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #ffffff; }

.promo__text {
  position: relative;
  margin-left: 95px;
  padding-bottom: 26px;
  font-family: "PT Mono", "PT Serif", monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 34px;
  color: #ffffff; }

.promo__text::before {
  position: absolute;
  content: "";
  height: 2px;
  width: 66px;
  background-color: #ffffff;
  left: -90px;
  top: 18px; }

.promo__button {
  margin-left: 95px;
  padding: 17px 45px;
  background-color: #00b2ff;
  border-radius: 5px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  outline: none; }

.promo__button:hover {
  opacity: 0.9; }

.promo__button:active {
  opacity: 0.8; }

.promo__scrolling {
  position: absolute;
  bottom: 24px;
  left: -35px;
  font-family: "PT Mono", "PT Serif", monospace;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  text-decoration: none;
  opacity: 0.5;
  transform: rotate(-90deg); }

@media (max-width: 1024px) {
  .promo {
    margin-top: 38px; }
  .promo__content {
    margin-right: 0px; }
  .promo__title {
    margin-left: 44px;
    margin-top: 75px;
    font-size: 60px;
    line-height: 70px; }
  .promo__text {
    margin-bottom: 25px;
    font-size: 18px;
    line-height: 156.8%; }
  .promo__text::before {
    top: 14px;
    left: -48px;
    width: 33px; }
  .promo__img {
    margin-right: -8px;
    width: 260px;
    height: 340px; }
  .promo__button {
    padding: 17px 27px; }
  .promo__scrolling {
    display: none; } }

@media (max-width: 768px) {
  .promo {
    flex-wrap: wrap;
    justify-content: flex-end;
    margin-right: 0px;
    margin-left: 0px;
    margin-top: 32px; }
  .promo__content {
    order: 1; }
  .promo__title {
    margin-top: -10%;
    margin-left: 16px;
    font-size: 40px;
    line-height: 47px; }
  .promo__text {
    margin-left: 67px;
    margin-right: 1%;
    margin-bottom: 40px;
    padding-bottom: 0px;
    font-size: 16px;
    line-height: 156.8%; }
  .promo__word {
    display: none; }
  .promo__img {
    order: -1;
    margin-right: 0px;
    width: 294px;
    height: 220px; }
  .promo__button {
    margin-right: 6%;
    margin-left: 6%;
    margin-bottom: 68px; }
  .promo__button::before {
    position: relative;
    content: "Бесплатная консультация"; } }
